body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.visible {
  visibility: visible;
  opacity: 1;
  -webkit-transition: opacity 5s linear;
  -o-transition: opacity 5s linear;
  transition: opacity 5s linear;
}
.hidden {
  visibility: hidden;
  opacity: 0;
  -webkit-transition: visibility 0s 2s, opacity 2s linear;
  -o-transition: visibility 0s 2s, opacity 2s linear;
  transition: visibility 0s 2s, opacity 2s linear;
}
/*main*/
#main-sect {
  background-image: url('../assets/img.jpg');
  background-size: contain;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100vw;
}
.overlay {
  background-color: rgba(0,0,0,0);
  height: 100%;
}
.deep-overlay {
  background-color: rgba(0,0,0,0.3);
  height: 100%;
}
.pattern-overlay {
  background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAAECAYAAACp8Z5+AAAAGklEQVQYV2NkYGD4D8SMQAwGcAY2AbBKDBUAVuYCBX45Xb8AAAAASUVORK5CYII=') repeat;
  height: 100%;
}
.right {
  float: right;
  text-align: right;
  margin-right: 8vw;
  max-width: 400px;
  padding-top: 120px;
  font-size: 20px;
  font-weight: 100;
  color: #333;
}
.hi {
  font-family: 'Pacifico', cursive;
  font-size: 70px;
}
.right a {
  color: #333 !important;
  padding-bottom: 2px;
  text-decoration: none !important;
  border-bottom: 2px dashed #333;
}
.right a:hover{
  border-bottom: 2px solid #333;
}
.right sup {
  font-size: 12px;
}
.form-control {
  border: 2px dashed #666;
  height: 50px;
  font-size: 20px;
  width: 300px;
  float: right;
}
a.send {
  margin-top: 20px;
  width: 200px;
  color: #fff !important;
  font-size: 20px;
  font-family: 'Pacifico', cursive;
  background: #9d2424;
  border: 2px dashed #9d2424 !important;
}
a.send:hover, .send:focus {
  background-color: #ffffff;
  color: #9d2424 !important;
}


@media (max-width:1024px) {
  #main-sect {
    background-size: cover;
    background-position: center;
  }
  .form-control {
    border:none;
  }
  .overlay {
    background-color: rgba(0,0,0,0.5);
  }
  .right {
    padding-top: 100px;
    margin-right: 5vw;
  }
  .right, .right a, .right a:hover {
    color: #fff !important;
    border-color: #fff;
  }
}
@media (max-width:500px) {
  #main-sect {
    height: 700px
  }
}

/*survey*/
.top {
  height: 400px;
  background-image: url('../assets/abstract.jpg');
  background-size: cover;
  background-attachment:fixed;
  background-position: center;
}
#clouds {
  margin-top: -100px;
  fill: #fff;
}
.top-title, .header {
  text-align: center;
  color: #fff;
  font-size: 60px;
  padding-top: 200px;
  font-family: 'Pacifico', cursive;
}
.header {
  width: 100vw;
  color: #333;
  padding-top: 20px;
}
.top-sub {
  text-align: center;
  font-size: 20px;
  color: #fff;
  font-weight: 100;
}
.second-section {
  margin-bottom: 20px;
}
.stroke {
  text-align: right;
  padding-top: 120px;
  font-size: 20px;
  font-weight: 100;
}
.laptop {
  padding-top: 30px;
  border-radius: 30%;
}
#curveDown, #curveUp {
  fill: #fff;
}
#curveUp {
  margin-top: 95px;
}
#zebra {
  background-image:url('../assets/zebra.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  height: 480px;
}
.community {
  font-size: 50px;
  text-align: center;
  padding-top:100px;
  color: #fff;
  font-family: 'Pacifico', cursive;
}
.coffee p {
  text-align: center;
}
.form-title {
  text-align: center;
  font-size: 20px;
}
.icons {
  text-align: right;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
code {
  background-color: #eee;
  padding: 5px;
  border-top: 2px dashed #9d2424 !important;
}
.icons i {
  color: #333;
  font-size: 20px;
  padding: 5px;
}
#triangle {
  fill: #fff;
}
#stars {
  background-image: url('../assets/stars.jpg');
  height: 200px;
  background-attachment: fixed;
}
.foot {
  font-size: 20px;
  color: #fff;
  text-align: center;
  font-family: 'Pacifico', cursive;
  padding-top: 50px;
}
.foot a {
  text-decoration: underline;
  color: #fff;
}



